import {EditorState, EditorView, basicSetup} from "@codemirror/basic-setup"
import { html } from "@codemirror/lang-html"
import { keymap } from '@codemirror/view';
import { expandAbbreviation } from '@emmetio/codemirror6-plugin';
import { abbreviationTracker } from "@emmetio/codemirror6-plugin";

let editor = new EditorView({
  state: EditorState.create({
    doc: "hello world",
    extensions: [basicSetup, html(), abbreviationTracker(),
                // Bind Expand Abbreviation command to keyboard shortcut
            keymap.of([{
                key: ' ',
                run: expandAbbreviation
            }]),]
  }),
  parent: document.querySelector("#editor")
})
 
function addSection(event) {

let transaction = editor.state.update({changes: {from: 6, to: 11, insert: "editor"}})
  alert("section");
}
 
window.onload = function () {
  var el = document.getElementById("addsection");
  el.addEventListener("click", addSection, false);
};